import { ContentPackage } from "@rp/react/tanstack-api/content"
import { UseQueryResult } from "@tanstack/react-query"
import { SourceSelectorSelectedPackageInfo } from "../sourceSelector.types"

function mapContentPackageToSelectedPackage(
  contentPackageResponse: UseQueryResult<ContentPackage, Error>,
): SourceSelectorSelectedPackageInfo {
  if (!contentPackageResponse.data) {
    return { id: "", name: "", sourceCount: 0 }
  }

  return {
    id: contentPackageResponse.data.id,
    name: contentPackageResponse.data.name,
    sourceCount: contentPackageResponse.data.sourceCount,
  }
}

export { mapContentPackageToSelectedPackage }
